h1, h2, h3, h4, h5, h6 {
  margin-top: 0;
}


html, body {
  margin: 0;
  padding: 0;
  overscroll-behavior: none;
  overflow-x: hidden;
}

body {
  background-color: #0a1a3f; 
  width:100%;
  font-family: 'Arial', sans-serif;
  overflow-x: hidden;
}

.top {
  margin-bottom: 2%;
  top:0;
  width:100%;
  height:25vh;
  background-color: #0F2757;
}

.top img{
  width:100%;
  height:auto;
  bottom: 0;
}

.project-list {
  top: 30vh;
  display: flex;
  flex-direction: row;
  justify-content: center;
  flex-wrap: wrap;
  gap: 16px;
  min-width: 300px;
  padding-bottom: 15vh;
}

@media (max-width: 768px) {
  .project-list {
    display: flex;
    flex-direction: column;
    align-items: center;
    flex-wrap: wrap;
    gap: px;
  }
}


h1 {
  font-family: 'VT323', monospace;
  color:whitesmoke;
  font-size: 4rem;
  margin-bottom: 8px;
  padding-left: 2%;
}

h2 {
  font-family: 'VT323', monospace;
  color:whitesmoke;
  font-size: 2rem;
  margin-top: 0px;
  margin-bottom: 8px;
  padding-left: 2%;
}

h4 {
  font-family: 'VT323', monospace;
  color:whitesmoke;
  font-size: 1.5rem;
  margin-top: 0px;
  margin-bottom: 8px;
  padding-left: 2%;
}

p {
  font-family: 'Inter', sans-serif;
  color:#2c2c2c;
}

.spacer {
  height: 8vh;
  width: 100%;
  bottom: 0;
}

/* Section divider between Skills and Bio */
.section-divider {
  width: 60vw;
  max-width: 900px; /* match project panel max width */
  height: 4px;
  margin: 4vh auto; /* center within page */
  background: rgba(255, 255, 255, 0.55); /* solid semi-transparent like panels */
  border-radius: 2px;
  box-shadow: 0 2px 12px rgba(0, 0, 0, 0.15);
  border: 1px solid rgba(255, 255, 255, 0.45);
  position: relative;
  z-index: 2; /* ensure it sits above fixed skyline */
}
